<template>
  <v-layout>
    <b-alert variant="danger"
             dismissible
             :show="showDismissibleAlert"
             @dismissed="showDismissibleAlert=false">
      {{alertTitle}}
    </b-alert>
    <v-card contextual-style="dark">

      <span slot="header">
        <span>登录/注册</span>

      </span>
      <div slot="body">
        <form>
          <div class="form-group">
            <div class="input-group">
              <div class="input-group-addon">
                <i class="fa fa-envelope fa-fw"></i>
              </div>
              <input
                v-model="user.email"
                type="email"
                placeholder="Email"
                class="form-control"
              >
            </div>
          </div>
          <div class="form-group">
            <div class="input-group">
              <div class="input-group-addon">
                <i class="fa fa-lock fa-fw"></i>
              </div>
              <input
                v-model="user.password"
                type="password"
                placeholder="Password"
                class="form-control"
              >
            </div>
          </div>
          <div class="form-group">
            <button class="btn btn-outline-primary" @click="login">
              Login
            </button>
            <button class="btn btn-outline-primary" @click="register">
              Register
            </button>
          </div>
        </form>
      </div>
      <!--<div slot="footer">-->
        <!--No account?-->
        <!--<router-link :to="{ name: 'register.index' }">Register</router-link>-->
      <!--</div>-->
    </v-card>

    <b-modal id="modal1" title="Bootstrap-Vue">
      <p class="my-4">Hello from modal!</p>
    </b-modal>
    <b-modal v-model="modalShow">
      Hello From Modal!
    </b-modal>
  </v-layout>
</template>

<script>
  /* ============
   * Login Index Page
   * ============
   *
   * Page where the user can login.
   */

  import VLayout from '@/layouts/NavLayout';
  import VCard from '@/components/Card';


  export default {
    /**
     * The name of the page.
     */
    name: 'login-index',

    /**
     * The data that can be used by the page.
     *
     * @returns {Object} The view-model data.
     */
    data() {
      return {
        user: {
          email: null,
          password: null,
        },
        modalShow: false,
        showDismissibleAlert: false,
        alertTitle: '系统错误',
      };
    },

    /**
     * The methods the page can use.
     */
    methods: {
      /**
       * Will log the user in.
       *
       * @param {Object} user The user to be logged in.
       */
      login() {
        console.log('login', this.user);

        this.$userlib.login(this.user.email, this.user.password, (err) => {
          if (err) {
            this.alertTitle = err;
            this.showDismissibleAlert = true;
            return
          }

          this.$router.push({name: 'todolist'})
        })

      },
      register() {
        console.log('register', this.user);
        this.$userlib.register(this.user.email, this.user.password, (err) => {
          if(err) {
            return this.modalShow = true;
          }

          alert('注册成功！')
        })
      },
    },

    /**
     * The components the page can use.
     */
    components: {
      VLayout,
      VCard,
    },
  };
</script>



